import React, { Component } from 'react';
import axios from 'axios';
import { styled } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import AppBar from './AppBar';
import Profile from './Profile';
import MainFoodList from './MainFoodList';

const MyFab = styled(Fab)({
  position: 'absolute',
  right: 16,
  bottom: 16,
});

export default class App extends Component {
  state = {
    brandedList: [],
  };

  componentDidMount = () => {
    const self = this;
    axios({
      method: 'get',
      baseURL: 'https://trackapi.nutritionix.com',
      url: '/v2/search/instant',
      headers: {
        'Content-Type': 'application/json',
        'x-app-id': 'be5f53ea',
        'x-app-key': '55738b9b843b79688e875d01a1c37e0f',
        'x-remote-user-id': '0',
      },
      params: {
        query: 'cheese',
        branded: true,
        // common: false,
        // detailed: true,
      },
      // data: {
      //   query: '1 cup of milk',
      //   num_servings: 0,
      //   aggregate: 'string',
      //   line_delimited: false,
      //   timezone: 'Australia/Sydney',
      //   locale: 'en_US',
      // },
    })
      .then(response => {
        console.log(response.data.branded);
        self.setState({
          brandedList: response.data.branded,
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  render() {
    const { brandedList } = this.state;

    return (
      <div>
        <AppBar />
        <Grid container>
          <Grid item xs={12} sm={4}>
            <Profile />
          </Grid>
          <Grid item xs={12} sm={8}>
            <MainFoodList list={brandedList} />
          </Grid>
        </Grid>
        <MyFab color="primary" aria-label="add">
          <AddIcon />
        </MyFab>
      </div>
    );
  }
}
