import React, { Fragment } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import styles from './MainFoodList.module.css';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: 'calc(100vh - 128px)',
    overflow: 'scroll',
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
  avatarRoot: {
    borderRadius: 8,
  },
}));

const MainFoodList = ({ list }) => {
  const classes = useStyles();
  return (
    <List className={classes.root}>
      {list.map(food => (
        <Fragment key={food.nix_item_id}>
          <ListItem alignItems="flex-start">
            <ListItemAvatar>
              <Avatar
                alt={food.food_name}
                classes={{
                  root: classes.avatarRoot,
                }}
                src={food.photo.thumb}
              />
            </ListItemAvatar>
            <ListItemText
              primary={
                <span className={styles.primary}>
                  <span>{food.food_name}</span>
                  <span>{Math.floor(Math.random() * 100)} Cal</span>
                </span>
              }
              secondary={
                <span className={styles.secondary}>
                  <span>
                    {food.serving_qty} {food.serving_unit} ({Math.floor(Math.random() * 100)} g)
                  </span>
                  <span>{Math.floor(Math.random() * 2) ? 'Dinner' : 'Lunch'}</span>
                </span>
              }
            />
          </ListItem>
          <Divider variant="inset" component="li" />
        </Fragment>
      ))}
    </List>
  );
};

export default MainFoodList;
